<template>
  <div>
    <div class="main-bg"></div>
    <nv-head page-type="" left_icon="keyboard_arrow_left" right_icon="n"></nv-head>

    <div class="main-body">
      <div class="block login-page">
        <div class="portrait">
          <img src="../assets/images/Login_03.jpg"></img>
        </div>

        <div class="input-group">
          <a><i class="large material-icons form-label">perm_contact_calendar</i></a>
          <input class="account input-control" 
                 id="account" 
                 v-model="login_form.account" 
                 placeholder="" 
                 type="text" />
        </div>

        <div class="input-group">
          <a><i class="large material-icons form-label">lock</i></a>
          <input class="input-control password" 
                 id="password" 
                 v-model="login_form.password" 
                 placeholder="" 
                 type="password" />
        </div>

        <button class="btn submit login" @click="login()"></button>

        <div class="tip">
          <a href="/forget" class="tip-left"><span>?</span></a>
          <a href="/signup"><span></span></a>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
require('../assets/scss/login.scss');
require('../assets/scss/main.scss');
import nvHead from '../components/header.vue';
import nvMenu from '../components/menu.vue';
import $ from 'webpack-zepto';
import config from '../config.js';
import Form from '../libs/forms.js';

export default {
  data () {
    return {
      login_form: {}
    }
  },
  methods: {
    login () {
      let form = new Form();
      form.validateData(['account', 'password'], this.login_form);
      if(!form.is_valid){
        this.$alert(form.error.message);
        if(form.error.key !== 'password'){
          $('#account').focus();
          return;
        }
        $('#' + form.error.key).focus();
        return;
      }
      $.ajax({
        type: 'POST',
        url: config.domain + '/login',
        data: this.login_form,
        success: (data, status, xhr) => {
          let user = data.user;
          this.$alert(data.message);
          window.window.sessionStorage.user = JSON.stringify(user);
          this.$store.dispatch('setUserInfo', user);
          this.$router.push({
            path: "/"
          });
          this.$alert();
        },
        error: this.$errorHandler
      });
    }
  },
  components: {
    nvHead,
    nvMenu
  }
};
</script>



